.counter__wrapper-filled {
  margin: 20px 0;
}

.counter__wrapper-filled .counter-wrap {
  width: 160px;
  height: 160px;
  margin: 0 auto;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #F4F4F4;
  box-shadow: inset 0 0 10px 0 rgba(0, 0, 0, 0.05);
  position: relative;
  z-index: 1;
}

.counter__wrapper-filled .counter-wrap:before {
  content: '';
  position: absolute;
  top: 20px;
  left: 20px;
  right: 20px;
  bottom: 20px;
  background: #fff;
  border-radius: 50%;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.05);
  z-index: -1;
}

.counter__wrapper-filled .counter-wrap .counter-tri {
  position: absolute;
  top: 20px;
  left: 20px;
  right: 20px;
  bottom: 20px;
  z-index: -2;
  transform: rotate(0deg);
  transition: all 4s ease;
}

.counter__wrapper-filled .counter-wrap .counter-tri > .before {
  position: absolute;
  width: 15px;
  top: -7px;
  left: 50%;
  height: 15px;
  background: var(--brand-primary);
  transform: translate(-50%, 0) rotate(45deg);
  z-index: -2;
}

.counter__wrapper-filled .counter-wrap svg {
  width: 186px;
  height: 186px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow: visible;
  pointer-events: none;
}

.counter__wrapper-filled .counter-wrap svg circle {
  stroke: var(--brand-primary);
  stroke-width: 6px;
  fill: none;
  stroke-dasharray: 584;
  stroke-dashoffset: 584;
  transform: rotate(-90deg);
  transform-origin: 93px 93px;
  transition: all 4s ease;
}

.counter__wrapper-filled .counter {
  font-family: 'Montserrat', sans-serif;
  font-size: 36px;
  color: var(--brand-primary);
  line-height: 1;
}

.counter__wrapper-invertible {
  margin: 20px 0;
}

.counter__wrapper-invertible .counter-wrap {
  width: 230px;
  height: 230px;
  margin: 0 auto;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(50deg, var(--brk-base-3), var(--secondary));
  overflow: hidden;
  position: relative;
}

.counter__wrapper-invertible .counter-wrap:before, .counter__wrapper-invertible .counter-wrap:after {
  content: '';
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  left: -7px;
  width: 14px;
  height: 14px;
  background: #fff;
  border-radius: 50%;
  z-index: 2;
}

.counter__wrapper-invertible .counter-wrap:after {
  left: auto;
  right: -7px;
}

.counter__wrapper-invertible .counter-wrap > .before {
  position: absolute;
  bottom: calc(50% - 1px);
  left: 0;
  right: 0;
  height: 50%;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.3), transparent);
  border-bottom: 2px solid var(--brk-secondary-2);
}

.counter__wrapper-invertible .counter {
  font-family: 'Montserrat', sans-serif;
  font-size: 74px;
  color: #fff;
  line-height: 1;
}

.counter__wrapper-clean {
  margin: 20px 0;
}

.counter__wrapper-clean .counter-wrap {
  width: 230px;
  height: 230px;
  margin: 0 auto;
  border-radius: 50%;
  display: flex;
  justify-content: center;
