}

.counter__wrapper-clean svg {
  width: 230px;
  height: 230px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-40deg);
  overflow: visible;
  pointer-events: none;
}

.counter__wrapper-roller {
