}

.counter__wrapper-roller .counter {
  font-family: 'Montserrat', sans-serif;
  font-size: 72px;
  color: var(--brand-primary);
  letter-spacing: 0.2em;
  line-height: 1;
}

.counter__wrapper-roller .odometer-formatting-mark {
  display: none;
}

.counter__wrapper-icon {
  text-align: left;
  max-width: 330px;
  margin: 20px auto;
  position: relative;
  z-index: 1;
  color: var(--brk-secondary-1);
}

.counter__wrapper-icon .livicon-evo {
  display: inline-block;
  position: absolute;
  width: 60px;
  text-align: center;
}

.counter__wrapper-icon .icon {
  position: absolute;
  top: 5px;
  left: 0;
  font-size: 64px;
  line-height: 64px;
}

.counter__wrapper-icon .cont {
