  font-size: 72px;
  font-weight: 600;
  line-height: 1;
  color: var(--brk-secondary-1);
}

.counter__wrapper-airport {
  margin: 20px auto;
}

@media (min-width: 1200px) {
  .counter__wrapper-airport {
    text-align: left;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
}

.counter__wrapper-airport p {
  margin-top: 20px;
}

@media (min-width: 1200px) {
  .counter__wrapper-airport p {
    margin-top: 0;
    margin-left: 50px;
  }
  [dir="rtl"] .counter__wrapper-airport p {
    margin-left: 0;
    margin-right: 50px;
  }
}

.counter__wrapper-airport .counter {
  display: inline-block;
  font-family: 'Montserrat', sans-serif;
  font-size: 72px;
  font-weight: 300;
  line-height: 1;
  color: #fff;
  background: linear-gradient(80deg, var(--brand-primary), var(--brk-base-5));
  box-shadow: 0 6px 30px 0 rgba(34, 103, 226, 0.6);
  position: relative;
  z-index: 1;
  word-wrap: normal;
  white-space: nowrap;
}

[dir="rtl"] .counter__wrapper-airport .counter {
  display: flex;
  flex-direction: row-reverse;
}

.counter__wrapper-airport .counter span {
  display: inline-block;
  width: 78px;
  text-align: center;
  padding: 20px 0;
  border-left: 2px solid #fff;
}

.counter__wrapper-airport .counter span:first-child {
  border-left: none;
}

.counter__wrapper-airport .counter:before {
  content: '';
  position: absolute;
  bottom: calc(50% - 4px);
  left: 0;
  right: 0;
  height: 50%;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.3), transparent);
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
}

.counter__wrapper-airport.brk-color .counter {
  background: linear-gradient(80deg, var(--brk-base-3), var(--secondary));
  box-shadow: 0 6px 30px 0 rgba(var(--brk-base-3-rgb), 0.6);
}

.counter__wrapper-gradient {
  margin: 20px 0;
}

.counter__wrapper-gradient .counter-wrap {
  width: 230px;
  height: 230px;
  margin: 0 auto;
  border: 8px solid #fff;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.counter__wrapper-gradient .counter {
  font-family: 'Montserrat', sans-serif;
  font-size: 74px;
  font-weight: 500;
  color: #fff;
  line-height: 1;
}

.counter__wrapper-gradient.brk-color .counter-wrap {
  background: linear-gradient(180deg, var(--brk-base-3), var(--brk-secondary-3));
  box-shadow: 0 6px 30px 0 rgba(var(--brk-base-3-rgb), 0.6);
}

.odometer.odometer-auto-theme,
.odometer.odometer-theme-default {
  display: inline-block;
  vertical-align: middle;
  position: relative;
}

.odometer.odometer-auto-theme .odometer-digit,
.odometer.odometer-theme-default .odometer-digit {
  display: inline-block;
  vertical-align: middle;
  position: relative;
}

.odometer.odometer-auto-theme .odometer-digit .odometer-digit-spacer,
.odometer.odometer-theme-default .odometer-digit .odometer-digit-spacer {
  display: inline-block;
  vertical-align: middle;
  visibility: hidden;
}

.odometer.odometer-auto-theme .odometer-digit .odometer-digit-inner,
.odometer.odometer-theme-default .odometer-digit .odometer-digit-inner {
  text-align: left;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  height: 110px;
}

.odometer.odometer-auto-theme .odometer-digit .odometer-ribbon,
.odometer.odometer-theme-default .odometer-digit .odometer-ribbon {
  display: block;
}

.odometer.odometer-auto-theme .odometer-digit .odometer-ribbon-inner,
.odometer.odometer-auto-theme .odometer-digit .odometer-ribbon-inner,
.odometer.odometer-theme-default .odometer-digit .odometer-ribbon-inner {
  display: block;
  -webkit-backface-visibility: hidden;
}

.odometer.odometer-auto-theme .odometer-digit .odometer-value,
.odometer.odometer-theme-default .odometer-digit .odometer-value {
  display: block;
  -webkit-transform: translateZ(0);
  padding: 20px 0;
}

.odometer.odometer-auto-theme .odometer-digit .odometer-value.odometer-last-value,
.odometer.odometer-theme-default .odometer-digit .odometer-value.odometer-last-value {
  position: absolute;
}

.odometer.odometer-auto-theme.odometer-animating-up .odometer-ribbon-inner,
.odometer.odometer-theme-default.odometer-animating-up .odometer-ribbon-inner {
  transition: transform 2s;
}

.odometer.odometer-auto-theme.odometer-animating-up.odometer-animating .odometer-ribbon-inner,
.odometer.odometer-theme-default.odometer-animating-up.odometer-animating .odometer-ribbon-inner {
  transform: translateY(-100%);
}

.odometer.odometer-auto-theme.odometer-animating-down .odometer-ribbon-inner,
.odometer.odometer-theme-default.odometer-animating-down .odometer-ribbon-inner {
  transform: translateY(-100%);
}

.odometer.odometer-auto-theme.odometer-animating-down.odometer-animating .odometer-ribbon-inner,
.odometer.odometer-theme-default.odometer-animating-down.odometer-animating .odometer-ribbon-inner {
  transition: transform 2s;
  transform: translateY(0);
}

